import {Component} from '@angular/core';
import {Events, IonicPage, LoadingController, NavController, NavParams} from 'ionic-angular';
import {CellModel} from "../../../models/cell-model";
import {Cell} from "../../../providers/cellgroups/cell";

@IonicPage()
@Component({
  selector: 'page-chat',
  templateUrl: 'select-cellgroups.html',
})
export class SelectCellgroups {


  cellgroups: CellModel= [];
  private obj: any;
  private bId: any;
    private phone: any;
    private uid: any;


  constructor(navParams: NavParams,
              private cellgroup: Cell,
              private events: Events,
              private  loading: LoadingController,
              public navCtrl: NavController,) {

    this.obj= navParams.get('data') || "None";
      this.uid = navParams.get('uid');
      this.phone = navParams.get('phone')
    this.bId = this.obj;
    console.log(this.bId)


  }

  ionViewWillLeave() {

  }

  ionViewDidEnter() {
    //get notice list
    this.getCellgroups();



  }




  getCellgroups() {
    const loader = this.loading.create({
      content: "Getting data",
    });
    loader.present();

    this.cellgroup.query(this.bId).then((res: any) => {
      this.cellgroups = JSON.parse(res.data);
      loader.dismissAll()
    });
  }




  openDp(obj){
    this.navCtrl.push('SelectDpt', {

      data: obj,
      bid: this.bId,
        uid:this.uid,
        phone:this.phone,

    });

  }



}



